.theme-switcher {
  cursor: pointer;
}

.theme-switcher__checkbox {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  position: absolute;
}

.checkbox {
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  transition: background-color .25s linear;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switcher__checkbox:checked + .checkbox {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.theme-switcher__checkbox:focus-visible + .checkbox {
  outline: 1px auto #000;
}

.checkbox:before {
  content: " ";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: transform .25s linear;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.theme-switcher__checkbox:checked + .checkbox:before {
  transform: translateX(20px);
}

.current-link, .header__nav-link:hover, .header__nav-link:focus {
  min-height: 34px;
